    .profile {
        max-width: 100%;
        height: 650px;
        background: $maincolor_first;
        &__body {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            &_card_of_user {
                margin-top: 134px;
                margin-right: 36px;
                width: 382px;
                height: 474px;
                background-color: $maincolor_third;
                box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 41.7776px 33.4221px rgba(0, 0, 0, 0.0503198), 0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275), 0px 12.5216px 10.0172px rgba(0, 0, 0, 0.035), 0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725), 0px 2.76726px 2.21381px rgba(0, 0, 0, 0.0196802);
                &__avatar {
                    width: 100%;
                    height: 240px;
                    margin-top: 20px;
                    justify-content: center;
                    align-items: center;
                    display: flex;
                    flex-direction: column;
                    &_username {
                        font-size: 24px;
                        line-height: 54px;
                    }
                    & img {
                        width: 140px;
                        height: 140px;
                    }
                }
                &__information {
                    display: flex;
                    flex-direction: row;
                    width: 100%;
                    margin-top: 20px;
                    justify-content: center;
                    &_user_prop {
                        display: flex;
                        flex-direction: column;
                        font-family: Georgia, Times, Times New Roman, serif;
                        color: $maincolor_first;
                        font-size: 20px;
                        line-height: 40px;
                    }
                    &_user_details {
                        display: flex;
                        flex-direction: column;
                        margin-left: 20px;
                        font-size: 18px;
                        line-height: 40px;
                    }
                }
            }
            &_settings {
                margin-top: 134px;
                width: 656px;
                height: 474px;
                background-color: $maincolor_third;
                box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 41.7776px 33.4221px rgba(0, 0, 0, 0.0503198), 0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275), 0px 12.5216px 10.0172px rgba(0, 0, 0, 0.035), 0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725), 0px 2.76726px 2.21381px rgba(0, 0, 0, 0.0196802);
                &__options {
                    display: flex;
                    flex-direction: row;
                    &__change_data {
                        box-sizing: border-box;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 328px;
                        height: 81px;
                    }
                    &__statistics {
                        box-sizing: border-box;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 328px;
                        height: 81px;
                    }
                }
                &__body {
                    display: flex;
                    justify-content: space-around;
                    flex-direction: column;
                    margin-top: 20px;
                    margin-left: 50px;
                    height: 75%;
                    &_count {
                        display: flex;
                        justify-content: space-around;
                        & span {
                            font-size: 30px;
                        }
                    }
                    & label {
                        font-size: 20px;
                    }
                    & div .location {
                        font-size: 19px;
                    }
                    &__change {
                        &_password,
                        &_information,
                        &_color {
                            &:hover {
                                color: rgb(194, 194, 194)
                            }
                        }
                    }
                    &__change_password_button {
                        margin-top: 20px;
                        background-color: $maincolor_third;
                        border: 2px solid $maincolor_first;
                        color: $maincolor_first;
                        display: block;
                        font-family: Georgia, Times, Times New Roman, serif;
                        font-size: 20px;
                        width: 250px;
                        padding: 10px;
                        &:hover {
                            background-color: $maincolor_first;
                            color: $maincolor_third;
                            cursor: pointer;
                        }
                        &:focus {
                            outline: none;
                            background-color: $aqua_light;
                            border: 2px solid $aqua_light;
                            color: $maincolor_third;
                            cursor: pointer;
                        }
                        &:active {
                            outline: none;
                        }
                    }
                    & div {
                        font-family: Georgia;
                        font-style: normal;
                        font-weight: normal;
                        font-size: 40px;
                        line-height: 45px;
                        cursor: pointer;
                    }
                }
            }
        }
    }
    
    .password__change {
        font-size: 20px;
        font-weight: normal;
    }
    
    .blue__mood {
        border: 1px solid $maincolor_first;
        background-color: $maincolor_first;
        opacity: 0.5;
        cursor: pointer;
        &:hover {
            opacity: 0.4;
        }
        & h2 {
            color: $maincolor_third;
            font-size: 30px;
            line-height: 34px;
            font-style: normal;
            font-weight: normal;
        }
        &_prop {
            color: $maincolor_first;
        }
        &_disabled {
            border: 1px solid $maincolor_first;
            cursor: pointer;
            &:hover {
                background-color: $maincolor_first;
                opacity: 0.5;
                & h2 {
                    color: $maincolor_third;
                }
            }
            & h2 {
                color: $maincolor_first;
                font-size: 30px;
                line-height: 34px;
                font-style: normal;
                font-weight: normal;
            }
        }
    }
    
    .yellow__mood {
        border: 1px solid $maincolor_second;
        background-color: $maincolor_second;
        opacity: 0.5;
        cursor: pointer;
        &:hover {
            opacity: 0.4;
        }
        & h2 {
            color: $maincolor_third;
            font-size: 30px;
            line-height: 34px;
            font-style: normal;
            font-weight: normal;
        }
        &_prop {
            color: $maincolor_second;
        }
        &_disabled {
            border: 1px solid $maincolor_second;
            cursor: pointer;
            &:hover {
                background-color: $maincolor_second;
                opacity: 0.5;
                & h2 {
                    color: $maincolor_third;
                }
            }
            & h2 {
                color: $maincolor_second;
                font-size: 30px;
                line-height: 34px;
                font-style: normal;
                font-weight: normal;
            }
        }
    }
    
    @media all and (max-width: 1112px) {
        .profile {
            max-width: 100%;
            height: 1268px;
            &__body {
                flex-direction: column;
            }
        }
    }
    
    @media all and (max-width: 936px) {
        .profile {
            max-width: 100%;
            height: 1400px;
            &__body {
                flex-direction: column;
                &_card_of_user {
                    margin-top: 230px;
                }
            }
        }
    }